export default function deferred(constructor) {
  let res, rej;

  const promise = new Promise((resolve, reject) => {
    if (constructor) {
      constructor(resolve, reject);
    }

    res = resolve;
    rej = reject;
  });

  promise.resolve = a => {
    res(a);
    return promise;
  };

  promise.reject = a => {
    rej(a);
    return promise;
  };

  return promise;
}
